.pin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 130px;
}

.pin h2 {
  @include reset-block;
  font-family: $garamond;
  font-size: 34px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 0.03em;
  margin-bottom: 32px;
}

.pin-link {
  color: $black;
  line-height: 130%;
  text-transform: capitalize;
  border-bottom: 1px solid $brown;
  margin-bottom: 62px;

  &:hover {
    color: $brown;
  }

  &:focus {
    color: $brown;
  }

  &:active {
    color: $brown;
    transform: scale(0.9);
  }
}

.pin-list {
  @include reset;
  display: grid;
  grid-template-columns: 1fr auto 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 27px;
  grid-row-gap: 34px;
}

.pin-item {
  &--wide {
    grid-row: span 2;
  }
}

.pin-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: $tablet-width) {
  .pin {
    margin-bottom: 80px;
  }

  .pin h2 {
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-width) {
  .pin {
    margin-bottom: 70px;
  }

  .pin-link {
    margin-bottom: 50px;
  }

  .pin-list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto;
    grid-column-gap: 24px;
    grid-row-gap: 27px;
  }

  .pin-item {
    &--wide {
      grid-row: span 2;
    }

    &--mobile-display {
      display: none;
    }
  }
}
